export default class Point {
  constructor(props) {
    this.init(props)
  }

  init(props) {
    this.x = props.x;
    this.y = props.y;
    this.realX = props.realX;
    this.realY = props.realY;
    if (!this.shape) {
      this.shape = props.shape
    }
  }

  reInit(props) {
    this.init(props)
    this.shape.reRender()
  }
}
