import Line from './Line';
import Point from './Point';

export default class Rectangle extends Line {
  constructor(props) {
    super(props);
    const p1 = new Point({shape: this, x: 200, y: 300});
    const p2 = new Point({shape: this, x: 500, y: 300});
    this.points = [p1, p2]
    this.strokeWidth = 140
  }

  static getShapeDetails() {
    return {
      name: 'Rectangle'
    }
  }

  removePoint(point) {
    //
  }

  fill(color) {
    this.strokeColor = color
    this.reRender()
  }
}
