export default function getPointParameters({
                                             distanceX,
                                             distanceY,
                                             vectorElBoundingClient,
                                             widthHeight,
                                             realX,
                                             realY,
                                             activeShape
                                           }) {
  return {
    x: distanceX, y: distanceY,
    realX: Number.parseInt(realX / ((vectorElBoundingClient.width) / 2) * widthHeight.x),
    realY: Number.parseInt(realY / (vectorElBoundingClient.width / 2) * widthHeight.y), shape: activeShape
  }
}
