import {cloneDeep, debounce} from 'lodash';

export default class BaseShape {
  constructor({reRender, zIndex, key, fillColor = '#00ff00'}) {
    this.key = key
    this.reRender = reRender
    this.fillColor = fillColor
    this.strokeColor = '#812ab9'
    this.strokeWidth = '1'
    this.debouncedSetFillColor = debounce(this.setFillColor, 10)
    this.debouncedSetStrokeColor = debounce(this.setStrokeColor, 10)
    this.zIndex = zIndex
    this.isDragging = false;
  }

  static getShapeDetails() {
    return {
      name: 'Unknown'
    }
  }

  getSvgStyles() {
    return {
      zIndex: this.zIndex
    }
  }

  getSvgId() {
    return this.key;
  }

  setFillColor(c) {
    this.fillColor = c;
    this.reRender()
  }

  setStrokeColor(c) {
    this.strokeColor = c;
    this.reRender();
  }

  setStrokeWidth(w) {
    this.strokeWidth = w;
    this.reRender()
  }

  onMouseClick() {
    //
  }

  onMouseMoveWhileDragging(e) {
    console.log(e)
  }

  move(x, y) {
    this.points.forEach(p => {
      p.x += x;
      p.y += y;
    })
    this.reRender();
  }

  clone() {
    const c = cloneDeep(this)
    c.prototype = this;

    return c;
  }

  fill(color) {
    this.fillColor = color
    this.reRender()
  }
}
