import {
  Box,
  Button,
  Paper,
  SvgIcon,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Tooltip,
  Typography
} from '@mui/material';
import {Add, Check, ColorLens, Delete, Edit, FormatPaintOutlined, OpenWithOutlined} from '@mui/icons-material';
import ShapeSelect from './ShapeSelect';
import Polygon from '../shapes/Polygon';
import {useState} from 'react';
import Popover from '@mui/material/Popover';

export default function ShapesCard({
                                     mode, setMode, shapes, setColor,
                                     activeShape, addShape, removeShape, activateShape, color
                                   }) {
  const [activeShapeClass, setActiveShapeClass] = useState(() => Polygon);
  const [colorAnchorEl, setColorAnchorEl] = useState(null);

  const onEditClicked = () => {
    setMode('edit')
  }

  const onPanClicked = () => {
    setMode('move')
  }

  const onColorClick = (event) => {
    setColorAnchorEl(event.currentTarget);
  };

  const onColorPopoverClose = () => {
    setColorAnchorEl(null);
  };

  const onFillColorClick = () => {
    setMode('color_fill')
  }

  return <Box pt={2} sx={{maxHeight: '500px', overflowY: 'auto'}} mb={4}>
    <Box sx={{display: 'flex', justifyContent: 'space-between', alignItems: 'center'}}>
      <Typography variant="h3" mb={2}>Shapes</Typography>
      <Box sx={{display: 'flex', gap: '10px', '& > *': {flexShrink: 0}}}>
        <Tooltip title="Edit">
          <Button color={mode === 'edit' ? 'warning' : 'success'}
                  onClick={onEditClicked}><SvgIcon component={Edit}/></Button>
        </Tooltip>
        <Tooltip title="Move">
          <Button color={mode === 'move' ? 'warning' : 'success'}
                  onClick={onPanClicked}><SvgIcon component={OpenWithOutlined}/></Button>
        </Tooltip>
        <Tooltip title="Choose Color">
          <Button onClick={onColorClick} sx={{color}}><SvgIcon component={ColorLens}/></Button>
        </Tooltip>
        <Tooltip title="Fill">
          <Button onClick={onFillColorClick} color={mode === 'color_fill' ? 'warning' : 'primary'}>
            <SvgIcon component={FormatPaintOutlined}/>
          </Button>
        </Tooltip>
        <Popover
            open={Boolean(colorAnchorEl)}
            anchorEl={colorAnchorEl}
            onClose={onColorPopoverClose}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'left',
            }}
        >
          <input value={color} onChange={e => setColor(e.target.value)} type="color"/>
        </Popover>
        <ShapeSelect value={activeShapeClass} onChange={setActiveShapeClass}/>
        <Button onClick={() => addShape(activeShapeClass)}>Add Shape<SvgIcon component={Add}/></Button>
      </Box>
    </Box>
    <TableContainer component={Paper}>
      <Table sx={{minWidth: 650}} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>#</TableCell>
            <TableCell align="right">Preview</TableCell>
            <TableCell align="right">Action</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {shapes.map((shape, index) => (
              <TableRow
                  key={index}
                  sx={{
                    '&:last-child td, &:last-child th': {border: 0},
                    background: activeShape == shape ? '#8aff84' : ''
                  }}
              >
                <TableCell component="th" scope="row">
                  {index + 1}
                </TableCell>
                <TableCell align="right">
                  <Box sx={{
                    position: 'relative', width: '50px', height: '50px', background: 'white',
                    border: '1px solid #2c2c2c'
                  }}>
                    {shape.getSvg()}
                  </Box>
                </TableCell>
                <TableCell align="right">
                  <Box sx={{display: 'flex', gap: '5px', justifyContent: 'end'}}>
                    <Button onClick={e => activateShape(shape)} color="success">
                      <SvgIcon component={Check}/>
                    </Button>
                    <Button onClick={e => removeShape(shape)} color="error">
                      <SvgIcon component={Delete}/>
                    </Button>
                    {shape.getSettings()}
                  </Box>
                </TableCell>
              </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  </Box>
}
