import {FormControl, InputLabel, MenuItem, Select} from '@mui/material';
import shapes from '../shapes'

export default function ShapeSelect({value, onChange}) {
  const onChangeWrapper = (e) => {
    onChange(() => shapes.find(s => s.getShapeDetails().name === e.target.value))
  }

  return (
      <FormControl>
        <InputLabel>Shape</InputLabel>
        <Select
            value={value.getShapeDetails().name}
            label="Shape"
            onChange={onChangeWrapper}
        >
          {shapes.map((s) => <MenuItem value={s.getShapeDetails().name}>{s.getShapeDetails().name}</MenuItem>)}
        </Select>
      </FormControl>
  );
}
