export default function getDistanceParameters(e) {
  const vectorEl = document.querySelector('.vector-container')
  const vectorElBoundingClient = vectorEl.getBoundingClientRect()
  const distanceY = Math.abs(Number.parseInt(vectorElBoundingClient.y - e.pageY));
  const distanceX = Math.abs(Number.parseInt(vectorElBoundingClient.x - e.pageX));
  const realY = distanceY + (vectorElBoundingClient.height / 2)
  const realX = -(distanceX + (vectorElBoundingClient.width / 2))

  return {realX, realY, distanceX, distanceY, vectorEl, vectorElBoundingClient}
}
