import './App.css';
import ArrowSvg from './components/ArrowSvg';
import {useEffect, useState} from 'react';
import ShapesCard from './components/ShapesCard';
import Polygon from './shapes/Polygon';
import {DownloadCard} from './components/DownloadCard';
import clsx from 'clsx';
import randomString from './utils/randomString';
import {throttle} from 'lodash';
import {Box} from '@mui/material';
import Footer from './components/Footer';

function App() {
  const [mode, setMode] = useState('edit')
  const [activeShape, setActiveShape] = useState(null)
  const [widthHeight] = useState({x: 700, y: 700});
  const [draggingPoint, setDraggingPoint] = useState(null)
  const [zIndex, setZIndex] = useState(1);
  const [draggingShape, setDraggingShape] = useState(null);
  const [color, realSetColor] = useState('#72e872')
  const [shapes, setShapes] = useState([])

  const setColor = throttle(realSetColor, 1000)

  const setDraggingPointWrapper = (point) => {
    if (!point) {
      if (!draggingPoint) return;
      draggingPoint.isDragging = false;
      setDraggingPoint(null)
    } else {
      setDraggingPoint(point);
    }
  }

  const addShape = (activeShapeClass) => {
    setMode('edit')
    setZIndex((oldZIndex) => oldZIndex + 1)
    setShapes((old) => {
      const newShape = new activeShapeClass({
        key: randomString(10), fillColor: color,
        reRender, setDraggingPoint: setDraggingPointWrapper, zIndex
      })
      setActiveShape(newShape)

      return [newShape, ...old]
    })
  }

  const removeShape = polygon => {
    if (polygon === activeShape) {
      setActiveShape(null)
    }
    setShapes((old) => old.filter(p => p !== polygon))
  }
  const activateShape = polygon => setActiveShape(polygon)

  const realRerender = () => setShapes(old => [...old])
  const reRender = throttle(realRerender, shapes.length < 20 ? 0 : 30 + shapes.length);
  shapes.forEach(s => s.reRender = reRender)

  useEffect(() => {
    document.removeEventListener('mouseup', () => setDraggingPointWrapper(null))
    document.addEventListener('mouseup', () => setTimeout(() => setDraggingPointWrapper(null)))
  }, [setDraggingPointWrapper])

  const onMouseMove = (e) => {
    if (mode === 'edit') {
      if (draggingPoint)
        draggingPoint.shape.onMouseMove({e, draggingPoint, widthHeight})
    } else if (mode === 'move') {
      if (draggingShape) {
        draggingShape.move(e.movementX, e.movementY)
      }
    }
  }

  const onMouseClick = (e) => {
    if (mode === 'color_fill') {
      const shape = findDraggingShape(e)
      if (shape) {
        shape.fill(color);
      }
      return;
    }

    if (mode === 'move') return;

    if (!activeShape) return;
    activeShape.onMouseClick({e, draggingPoint, activeShape, widthHeight})
  }

  const findDraggingShape = (e) => {
    const findSvg = (element) => {
      if (element.tagName === 'svg') {
        return element;
      }
      if (element.parentElement) {
        return findSvg(element.parentElement);
      }
      return null
    }
    const svgEl = findSvg(e.target);
    if (!svgEl) return null;
    return shapes.find(s => s.key === svgEl.id);
  }

  const onMouseDown = (e) => {
    if (mode !== 'move') return;
    const movingShape = findDraggingShape(e)
    if (movingShape) {
      setDraggingShape(movingShape)
      setActiveShape(movingShape)
    }
  }

  const onMouseUp = (e) => {
    setDraggingShape(null)
  }

  const onMouseLeave = (e) => {
    setDraggingShape(null)
  }

  const download = (options) => Polygon.download(shapes, options)

  const renderingShapes = shapes.map(p => p.getSvg({active: p === activeShape}));

  return (
      <>
        <div className="App">
          <Box sx={{flex: 1}}>
            <ShapesCard color={color} setColor={setColor} mode={mode} setMode={setMode} activateShape={activateShape}
                        removeShape={removeShape} addShape={addShape} activeShape={activeShape} shapes={shapes}/>
            <DownloadCard download={download}/>
          </Box>
          <Box onClick={onMouseClick} onMouseMove={onMouseMove} onMouseDown={onMouseDown}
               onMouseUp={onMouseUp} onMouseLeave={onMouseLeave}
               className={clsx('vector-container', mode + '__mode')}>
            <ArrowSvg className="vector-y-arrow"/>
            <ArrowSvg className="vector-x-arrow"/>
            <div className="vector-x-max">{widthHeight.x}</div>
            <div className="vector-y-max">{widthHeight.y}</div>
            <div className="vector-y-axios"></div>
            <div className="vector-x-axios"></div>
            {renderingShapes}
          </Box>
        </div>
        <Footer/>
      </>
  );
}

export default App;
