import {renderToStaticMarkup} from "react-dom/server"
import getDistanceParameters from '../helpers/getDistanceParameters';
import getPointParameters from '../helpers/getPointParameters';
import Point from './Point';
import {Input} from '@mui/material';
import BaseShape from './BaseShape';

export default class Polygon extends BaseShape {
  constructor(props) {
    const {points = [], setDraggingPoint} = props;
    super(props);
    this.points = points
    this.setDraggingPoint = setDraggingPoint
  }

  static getShapeDetails() {
    return {
      name: 'Polygon'
    }
  }

  static download(polygons, options) {
    const svgData = renderToStaticMarkup(<svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" x="0px"
                                              y="0px"
                                              viewBox="0 0 700 700" style={{enableBackground: 'new 0 0 700 700'}}>
      {polygons.sort(p => -p.zIndex).map(p => p.getSvgPolygon())}
      {options.preservePoints && polygons.map(p => p.getSvgCircles())}
    </svg>)
    var svgBlob = new Blob([svgData], {type: "image/svg+xml;charset=utf-8"});
    var svgUrl = URL.createObjectURL(svgBlob);
    var downloadLink = document.createElement("a");
    downloadLink.href = svgUrl;
    downloadLink.download = "output.svg";
    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);
  }

  addPoint(point) {
    this.points.push(point)
    this.reRender()
  }

  removePoint(point) {
    if (point.isDragging) return;
    this.points = this.points.filter(p => {
      return p !== point
    })
    this.reRender()
  }

  getSvg(options = {}) {
    return <svg id={this.getSvgId()} style={this.getSvgStyles()} viewBox="0 0 700 700"
                className={"svg " + (options.active ? 'active' : '')}>{this.getSvgPolygon()}{this.getSvgCircles()}</svg>;
  }

  getSvgPolygon() {
    const points = this.points.map(point => `${point.x},${point.y}`).join(' ')

    return <g style={this.getSvgStyles()}>
      <polygon points={points} style={{fill: this.fillColor, stroke: this.strokeColor, strokeWidth: this.strokeWidth}}/>
    </g>;
  }

  getSvgCircles() {
    return this.points.map(point =>
        <circle onClick={() => this.removePoint(point)}
                onMouseDown={() => this.setDraggingPoint(point)}
                className="circle" cx={point.x} cy={point.y} r="5" stroke="black" strokeWidth="2" fill="blue"/>)
  }

  onMouseMove({e, draggingPoint, widthHeight}) {
    if (draggingPoint) {
      draggingPoint.isDragging = true
      const {vectorElBoundingClient, distanceY, distanceX, realX, realY} = getDistanceParameters(e)
      draggingPoint.reInit(getPointParameters(
          {widthHeight, distanceX, distanceY, vectorElBoundingClient, realY, realX, activePolygon: this}))
    }
  }

  onMouseClick({e, draggingPoint, activeShape, widthHeight}) {
    if (draggingPoint) return;
    if (e.target.classList.contains('circle')) return;
    if (!activeShape) return;
    const {vectorElBoundingClient, distanceY, distanceX, realX, realY} = getDistanceParameters(e)
    activeShape.addPoint(new Point(getPointParameters(
        {widthHeight, distanceX, distanceY, vectorElBoundingClient, realY, realX, activeShape})))
  }

  getSettings() {
    return <>
      <Input sx={{width: '40px'}} value={this.strokeWidth} type="number"
             onChange={e => e.target.value >= 0 && this.setStrokeWidth(e.target.value)}/>
      <input type="color" onChange={(e) => this.debouncedSetFillColor(e.target.value)} value={this.fillColor}/>
      <input type="color" onChange={(e) => this.debouncedSetStrokeColor(e.target.value)} value={this.strokeColor}/>
    </>
  }
}
