import {Box, Button, Card, CardContent, SvgIcon, Typography} from '@mui/material';
import {Download} from '@mui/icons-material';
import {useEffect, useState} from 'react';
import Checkbox from '@mui/material/Checkbox';
import Celebration from './Celebration';
import {CSSTransition} from 'react-transition-group';

export function DownloadCard({download}) {
  const [preservePoints, setPreservePoints] = useState(false)
  const [showCelebration, setShowCelebration] = useState(false)

  useEffect(() => {
    return () => {
      if (!preservePoints) {
        document.body.classList.add('preserve-points')
      } else {
        document.body.classList.remove('preserve-points')
      }
    };
  }, [preservePoints]);
  useEffect(() => {
    setTimeout(() => document.body.classList.remove('preserve-points'))
  }, []);


  const removeCelebration = () => {
    setShowCelebration(false)
  }

  const downloadWrapper = () => {
    setShowCelebration(true)
    download({preservePoints})
  }

  return <Card sx={{marginTop: '20px', position: 'relative'}}>
    <CardContent>
      <Typography variant="h6" sx={{fontWeight: 'bold', textAlign: 'start'}}>Export your artwork!</Typography>
      <Box sx={{display: 'flex', justifyContent: 'space-between'}}>
        <Box sx={{display: 'flex', gap: '5px', alignItems: 'center'}}>
          <Typography>Preserve Points: </Typography>
          <Checkbox value={preservePoints} onChange={e => setPreservePoints(e.target.checked)}/>
        </Box>
        <Button onClick={downloadWrapper}><SvgIcon component={Download}/>Export</Button>
      </Box>
      <CSSTransition
          in={showCelebration}
          classNames="fade"
          timeout={300}
          unmountOnExit
      >
        <Celebration removeMe={removeCelebration}/>
      </CSSTransition>
    </CardContent>
  </Card>
}
