import Point from './Point';
import {Input} from '@mui/material';
import getDistanceParameters from '../helpers/getDistanceParameters';
import BaseShape from './BaseShape';

export default class Circle extends BaseShape {
  constructor(props) {
    const {setDraggingPoint} = props;
    super(props);
    this.cx = 350
    this.cy = 350
    this.r = 50
    this.setDraggingPoint = setDraggingPoint
    this.addPoints()
    this.dragging = false
  }

  static getShapeDetails() {
    return {
      name: 'Circle'
    }
  }

  addPoints() {
    this.points = [];
    for (let i = 1; i <= 4; i++) {
      const x = i === 1 || i === 4 ? this.cx - this.r : this.cx + this.r;
      const y = i === 1 || i === 2 ? this.cy - this.r : this.cy + this.r;
      const point = new Point({x, y, shape: this})
      point.number = i;
      this.points.push(point);
    }
  }

  getSvg(options = {}) {
    return <svg id={this.getSvgId()} style={this.getSvgStyles()} viewBox="0 0 700 700"
                className={"svg " + (options.active ? 'active' : '')}>{this.getSvgPolygon()}{this.getSvgCircles()}</svg>;
  }

  getSvgPolygon() {
    return <g style={this.getSvgStyles()}>
      <circle cx={this.cx} cy={this.cy} r={this.r}
              style={{fill: this.fillColor, stroke: this.strokeColor, strokeWidth: this.strokeWidth}}/>
    </g>;
  }

  getSvgCircles() {
    return this.points.map(point =>
        <circle onMouseDown={() => this.setDraggingPoint(point)}
                className="circle" cx={point.x} cy={point.y} r="4" stroke="black" strokeWidth="1" fill="blue"/>)
  }

  onMouseMove({e, draggingPoint, widthHeight}) {
    if (draggingPoint) {
      draggingPoint.isDragging = true
      let {distanceX} = getDistanceParameters(e)
      distanceX = Math.abs(distanceX)
      let diff = 0
      let cx, cy, r;
      if (draggingPoint.number === 1 || draggingPoint.number === 4) {
        diff = draggingPoint.x - distanceX
        cx = this.cx + diff
        cy = this.cy - diff
        r = diff > 0 ? (this.r + Math.sqrt(2) * diff) : (this.r - Math.sqrt(2) * Math.abs(diff))
      } else {
        diff = distanceX - draggingPoint.x
        cx = this.cx - diff
        cy = this.cy + diff
        r = diff > 0 ? (this.r + Math.sqrt(2) * diff) : (this.r - Math.sqrt(2) * Math.abs(diff))
      }
      if (r > 0) {
        this.cx = cx;
        this.cy = cy;
        this.r = r;
        this.addPoints()
        this.setDraggingPoint(this.points.find(p => p.number === draggingPoint.number))
        this.reRender();
      }
    }
  }

  move(x, y) {
    this.cx += x;
    this.cy += y;
    this.addPoints();
    this.reRender();
  }

  getSettings() {
    return <>
      <Input sx={{width: '40px'}} value={this.strokeWidth} type="number"
             onChange={e => e.target.value >= 0 && this.setStrokeWidth(e.target.value)}/>
      <input type="color" onChange={(e) => this.debouncedSetFillColor(e.target.value)} value={this.fillColor}/>
      <input type="color" onChange={(e) => this.debouncedSetStrokeColor(e.target.value)} value={this.strokeColor}/>
    </>
  }
}
