import {Box, Typography} from '@mui/material';
import GithubIcon from '../assets/images/Github.png'

export default function Footer(props) {
  return (
      <Box boxShadow={1} sx={{
        position: 'absolute', bottom: '0', left: '50%',
        background: '#dcdcdc40',
        transition: '200ms all',
        '&:hover': {
          background: '#dcdcdc60',
        },
        width: '100%',
        padding: '4px 0',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        transform: 'translate(-50%)'
      }}>
        <Typography><a target="_blank" href="https://github.com/parsagholipour">
          <Box height="16px" mr={1} as="img" src={GithubIcon}></Box>
          Parsa Gholipour
        </a></Typography>
      </Box>
  );
}
